html {
  width: 100%;
  height: 100%;
  // This defines what 1rem is. Browser default font size is 16px. With this, we set our global font-size to 10px.
  font-size: 62.5%; //1rem = 10px
  // overflow-x: hidden;
}

body {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow-x: hidden;

  position: relative;
  width: 100%;
  height: 100%;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

a {
  text-decoration: none;
}
